nav {
    border-top: 1px solid white;
    background-color: black;
    .header-container{
        
        padding: 10px 0 0 10px;
        width: 90%;
        margin: 0 auto;
        .principal-links {
            display: flex;
            justify-content: space-between;
            font-size: 25px;
            text-decoration: none;
            

            a{
                color: white;
                width: 30%;
            }

            .active{
                border-bottom: 10px solid white;
            }

        }

        .principal-links a:hover {
            color: greenyellow;
        }
    }
}